import React,{useState,useEffect} from "react";
import FormComponent from "../../components/form/form.component";
import axios from "axios";
const Providers = () => {
    const [state, setState] = useState({ provider_name: "", more_info: "" });
    const inputs = [{label:'שם הספק',name:'provider_name',type:'text'},{label:'עוד מידע',name:'more_info',type:'text'}]
    const title = 'הוסף ספק'
    const handleChange = (event) => {
        const { name, value } = event.target;
        setState(prevState => ({ ...prevState, [name]: value }));
    }
    useEffect(() => {
        
        return () => {
            console.log(state)
        };
    }, [state]);
    const handleSubmit = async (event) => {
        event.preventDefault();
        await axios.post('http://localhost:5000/add_provider',{state}).then(response => {
            if(!response.msg){
               alert("הספק נוסף בהצלחה")
            }else{
               
            }
        })

    }
    return (

        <FormComponent inputs = {inputs}  handleChange ={handleChange} handleSubmit = {handleSubmit} title ={title}/>

    )
}
export default Providers