import FormComponent from "../../components/form/form.component";
import React,{useState,useEffect} from "react";
import axios from "axios";
const FormPromotional = () => {
    const [state, setState] = useState({ provider_name: "", more_info: "" ,product_price:"",product_name:''});
    const [options, setOptions] = useState(null);
    const inputs = [{label:'שם המוצר',name:'product_name',type:'text'},{label:'מחיר',name:'product_price',type:'number'},{label:'עוד מידע',name:'more_info',type:'text'}]
    const title = 'הוסף מוצר לזיכוי'
    const handleChange = (event) => {
        const { name, value } = event.target;
        setState(prevState => ({ ...prevState, [name]: value }));
    }
    useEffect(() => {
        (async function() {
            try {
                const response = await fetch('http://localhost:5000/get_providers');
                const json = await response.json();
                setOptions(json.providers.map(pro => pro.name));
            } catch (e) {
                console.error(e);
            }
        })();
    },[]);

    const handleSubmit = async (event) => {
        event.preventDefault();
        await axios.post('http://localhost:5000/add_product',{state}).then(response => {
            if(!response.msg){
               alert("המוצר נוסף בהצלחה")
            }else{
               
            }
        })

    }
    return(
        <FormComponent inputs = {inputs}  handleChange ={handleChange} handleSubmit = {handleSubmit} title ={title} selectOptions = {options}/>
    )


}

export default FormPromotional