import React from "react";
import { Navbar,Container,Nav,NavDropdown } from 'react-bootstrap';
import { Link } from 'react-router-dom';
const NavBar =  () => {

    return(
<Navbar bg="light" expand="lg">
  <Container>
    <Navbar.Brand href="#home">Rafi's warehouse</Navbar.Brand>
    <Navbar.Toggle aria-controls="basic-navbar-nav" />
    <Navbar.Collapse id="basic-navbar-nav">
      <Nav className="me-auto">
        <Nav.Link href="/">Home</Nav.Link>
        <Nav.Link  href="/add_provider">Add Provider</Nav.Link>
        <NavDropdown title="More Actions" id="basic-nav-dropdown">
          <NavDropdown.Item href="#action/3.1">Show Last month</NavDropdown.Item>
          <NavDropdown.Item href="#action/3.2">show per Provider</NavDropdown.Item>
          <NavDropdown.Item href="#action/3.3">Export Dynamic Report</NavDropdown.Item>
        </NavDropdown>
      </Nav>
    </Navbar.Collapse>
  </Container>
</Navbar>
    )



}
export default NavBar