import React from "react";
import { Form,Button ,Col,Container,Row} from 'react-bootstrap';
import Input from "./input.component";
import SelectComponent from "./select.component";

const FormComponent = ({inputs,handleSubmit,handleChange,title,selectOptions}) => {
return(
    
   <Container className ="mt-5 mr-5"  >
    <h1 className="display-5">{title}</h1>
    <Form  onSubmit = {(event) => handleSubmit(event)}>
    {inputs.map(input => <Input key = {input.name} type = {input.type}  label = {input.label} handleChange = {handleChange} inputname = {input.name} />  
    )}
    {selectOptions ?
        
    <SelectComponent options = {selectOptions} handleChange = {handleChange}/>
    :
    null
    }
    <Button variant="primary" type="submit">
    Submit
    </Button>
    </Form>
    </Container>
   
)
}

export default FormComponent
